'use client'

import {Textarea} from "@/components/ui/textarea";
import {ArrowUp} from "lucide-react";
import {Button} from "@/components/ui/button";
import {useState} from "react";
import {getAnswer} from "@/api/textResponse";

const Chat = () => {
    const [value, setValue] = useState<string>("");

    const onSubmit = async () => {
        const res = await getAnswer(value);

        console.log(res)
    }

    return (
        <div className="w-full h-full bg-dark-bg-secondary flex flex-col justify-center items-center gap-8">
            <h1 className="text-4xl">Что сегодня в повестке дня?</h1>
            <div className="flex flex-col justify-center items-end gap-2 max-w-[50%] w-full bg-dark-bg-primary rounded-xl p-1 pb-3">
                <Textarea
                    className="border-0 focus-visible:ring-0 resize-none"
                    value={value}
                    onChange={(event) => setValue(event.target.value)}
                    placeholder="Введите запрос..."
                />
                <Button className="p-2 rounded-full w-fit bg-dark-text-primary hover:bg-dark-text-secondary mr-2" disabled={value === ''} onClick={onSubmit}>
                    <ArrowUp size={18} className="stroke-2 stroke-dark-bg-primary"/>
                </Button>
            </div>
        </div>
    );
};

export default Chat;
